let cycle;

// 弹幕字体颜色
function getRandomColor() {
	let rgb = []
	for (let i = 0; i < 3; ++i) {
		let color = Math.floor(Math.random() * 256).toString(16)
		color = color.length == 1 ? '0' + color : color
		rgb.push(color)
	}
	return '#' + rgb.join('')
}

export default {
	name: 'l-barrage',
	props: {
		minTime: {
			type: Number,
			default: 10
		},
		maxTime: {
			type: Number,
			default: 15
		},
		minTop: {
			type: Number,
			default: 0
		},
		maxTop: {
			type: Number,
			default: 30
		}
	},
	data() {
		return {
			items: [],
		}
	},
	methods: {
		add(text = '', time = Math.ceil(Math.floor(Math.random() * (this.maxTime - this.minTime + 1) + this.minTime))) {
			this.items.push({
				text,
				time,
				top: Math.ceil(Math.random() * (this.maxTop - this.minTop + 1) + this.minTop),
				color: getRandomColor(),
				display: 1,
			});
		},
		start(items = []) {
			this.items = [];
			cycle && (clearInterval(cycle));
			let i = 0,
				len = items.length;

			cycle = setInterval(() => {
				let time = 5;

				time = Math.ceil(Math.floor(Math.random() * (this.maxTime - this.minTime + 1) + this.minTime));


				if (i < len) {
					this.add(items[i], time);
					i++;
				} else {
					clearInterval(cycle);
					setTimeout(() => {
						this.$emit("end", {});
					}, time * 400 * len)
				}
			}, 500)
		},
		over(){
			clearInterval(cycle);
		}
	}
}
